@import "../const.sass";

.blockWrap{
  padding: 48px 0;
  &:nth-child(even){
    background: $block-bg-blue;
  }

  &:nth-child(odd){
    background: $block-bg-white;
  }

  .ant-row-flex{
    margin-top: 20px;
  }

  :global(.ant-card-meta-description){
    display: -webkit-box;
    min-height: 64px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }  
}
